import React, {Component} from "react"

import "./examcomponents/examstyles/examstyle.css"

import QuizService from "./examcomponents/algebraquestions.js"
import QuestionBox from "./examcomponents/QuestionBox.js"
import Result from "./examcomponents/Result.js"

class Algebra extends Component {

    state = {
        questionBank: [],
        score: 0,
        responses: 0
    }

    getQuestions = () => {
        QuizService().then(question => {
            this.setState({
                questionBank: question
            })
        })
    }

    computeAnswer = (answer, correctAnswer) => {

        if (answer === correctAnswer) {
            this.setState ({
                score: this.state.score + 1
            })
        }

        this.setState ({
            responses: this.state.responses < 5 ? this.state.responses + 1 : 5
        })
    }

    playAgain = () => {
        this.getQuestions()
        this.setState ({
            score: 0,
            responses: 0
        })
    }

    componentDidMount() {
        this.getQuestions()
    }

    render() {
        return (
            <div className = "quiz-container">
                <div className = "title">Examen de álgebra</div>
                    {this.state.questionBank.length > 0 && this.state.responses < 5 && this.state.questionBank.map(
                        ({question, answers, correct, questionId}) => (
                            <QuestionBox
                                question = {question}
                                options = {answers}
                                key = {questionId}
                                selected = {answer => this.computeAnswer(answer, correct)}
                            />
                        )
                    )}

                {this.state.responses === 5 ? (<Result score = {this.state.score} playAgain = {this.playAgain}/>) : null}
            </div>
        )
    }
}

export default Algebra